const Filters = ({filterProducts}) => {
    const drawerWidth = 190;
    const useStyles = makeStyles((theme) => ({
        root: {
            display: 'flex',
        },
        appBar: {
            zIndex: theme.zIndex.drawer + 1,
        },
        drawer: {
            width: drawerWidth,
            flexShrink: 0,
        },
        drawerPaper: {
            width: drawerWidth,
            top:'8%',
        },
        drawerContainer: {
            overflow: 'hidden',
        },
        content: {
            flexGrow: 1,
            padding: theme.spacing(3),
        },
    }));

    const [mobileMoreAnchorEl, setMobileMoreAnchorEl] = useState(null);
    const [products, setProducts] = useState([]);
    const location = useLocation();
    const classes = useStyles();

    const error = '[gilad, jason, antoine].filter((v) => v).length !== 2';
    const handleFilterProducts = (key) => filterProducts(key);


    return (
        <>
            <Drawer
                className={classes.drawer}
                variant="permanent"
                classes={{
                    paper: classes.drawerPaper,
                }}
            >
                <Toolbar />
                <div className={classes.drawerContainer}>
                    <FormControl component="fieldset" className={classes.formControl}>
                        <FormLabel component="legend">SORT</FormLabel>
                        <FormGroup>
                            <FormControlLabel
                                control={<Checkbox onChange={handleFilterProducts('women')} name="women" />}
                                label="Women"
                            />
                            <FormControlLabel
                                control={<Checkbox onChange={handleFilterProducts('men')} name="men" />}
                                label="Men"
                            />
                            <FormControlLabel
                                control={<Checkbox onChange={handleFilterProducts('kids')} name="kids" />}
                                label="Kids"
                            />
                        </FormGroup>
                    </FormControl>
                    <FormControl component="fieldset" className={classes.formControl}>
                        <FormLabel component="legend">SIZES</FormLabel>
                        <FormGroup>
                            <FormControlLabel
                                control={<Checkbox onChange={handleFilterProducts()} name="s" />}
                                label="S"
                            />
                            <FormControlLabel
                                control={<Checkbox onChange={handleFilterProducts()} name="m" />}
                                label="M"
                            />
                            <FormControlLabel
                                control={<Checkbox onChange={handleFilterProducts()} name="l" />}
                                label="L"
                            />
                        </FormGroup>
                    </FormControl>
                </div>
            </Drawer>
        </>
    );
};

export default Filters;


